.dark.titleHeader, .dark.navBarHeader{
    background: #3c3f41;
    color: lightgreen;
    transition-duration: 0.25s;
}
.light.titleHeader,  .light.navBarHeader{
    background: #282c34;
    color: #61dafb;
    transition-duration: 0.25s;
}

.vLine {
    margin: 0;
    width: 3px;
    height: 100%;
}
.mainContainer{
    display: flex;
    position: static;
    width: 100%;
    flex-direction: row;
    align-items: center;
    font-family: "Segoe Print", "Comic Sans MS", sans-serif;
}
.mainContainer.titleHeader{
    height: 50px;
}
.mainContainer.navBarHeader{
    height: 56px;
}
.spacer{
    height: 107px;
}
.headingText {
    margin-bottom: 3px;
    padding-left: 10px;
    font-size: 28px;
}
.icon.titleHeader {
    cursor: pointer;
    height: 32px;
    border-radius: 50%;
    transition-duration: 0.15s;
    right: 20px;
    position: absolute !important;
}
.titleNav{
    text-decoration: none;
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
}

.tabContainer{
    text-decoration: none;
    flex: 1 1 0;
    text-align: center;
    vertical-align: center;
    height: 73%;
    width: 100%;
    border: none;
    border-top: solid 12px transparent;
    font-size: 18px;
    border-bottom: solid transparent 3px;
}
.tabContainer.light{
    color: #7FF8FF;
    background: linear-gradient(to bottom, rgba(144,238,144,0) 20%,
    rgba(144,238,144,1));
    background-size: 100% 750%;
    animation: Animation2 0.4s ease forwards;
    transition: 0.25s;
}
.tabContainer.dark{
    color: #E1FFE1;
    background: linear-gradient(to bottom, rgba(97,218,251,0) 20%,
    rgba(97,218,251,1));
    background-size: 100% 750%;
    animation: Animation2 0.4s ease forwards;
    transition: 0.25s;
}
.tabContainer.light.selected{
    border-bottom: solid greenyellow 3px;
    transition: 0.5s;
}
.tabContainer.dark.selected{
    border-bottom: solid #61dafb 3px;
    transition: 0.5s;
}
.icon:hover{
    background: rgba(255, 255, 255, 0.15);
    transition-duration: 0.25s;
}
@keyframes Animation {
    0%{background-position:0 7%}
    100%{background-position:0 23%}
}
@keyframes Animation2 {
    0%{background-position:0 23%}
    100%{background-position:0 7%}
}

.tabContainer.dark:hover{
    background: linear-gradient(to bottom, rgba(97,218,251,0) 20%,
    rgba(97,218,251,1));
    background-size: 100% 750%;
    animation: Animation 0.4s ease forwards;
}
.tabContainer.light:hover{
    background: linear-gradient(to bottom, rgba(144,238,144,0) 20%,
    rgba(144,238,144,1));
    background-size: 100% 750%;
    animation: Animation 0.4s ease forwards;
}